<script>
import car from "./car";
export default {
  props: ["guid", "title", "time", "many", "fg", "how", "src"],
  data() {
    return {
      ctgs: [],
      goods: [],
      ctgText: "",
      count: ""
    };
  },
  components: {
    car
  },
  created() {
    //初始化购物车
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []);
    }
    //获取信息
    this.jQuery.get(this.apiURL + "/ctgs", result => {
      if (result.errcode == 0) {
        this.ctgs = result.data;
      }
    });
  },
  methods: {
    //获取信息
    showGoods(text, event) {
      this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
        if (result.errcode == 0) {
          this.goods = result.data;
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
      //二级菜单样式改变
      this.jQuery(".son2").css({ color: "#666", borderRightColor: "#e8e8e8" });
      this.jQuery(".son2").removeClass("act");
      this.jQuery(event.target).css({
        color: "#47b34f",
        borderRightColor: "#fff"
      });
      this.jQuery(event.target).addClass("act");
    },
    addCar(gd) {
      //添加商品
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        //判断是否存在相同的商品
        if (car[i].guid == gd.guid) {
          if (gd.num >= gd.store) {
            gd.num = gd.store - 1;
          }
          var num = ++this.bus.cars[this.guid][i].num;
          this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);
          this.$set(this.bus.carnums, gd.guid, num);
          //   console.log(this.bus.cars);
          return;
        }
      }
      if (gd.store == 0) {
        return;
      }
      gd.num = 1;
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.guid].push(gd); //添加商品
      this.$set(this.bus.carnums, gd.guid, 1);
      //   console.log(this.bus.cars);
    },
    subCar(gd) {
      //减商品
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        //判断是否存在相同的商品
        if (car[i].guid == gd.guid) {
          var num = --this.bus.cars[this.guid][i].num;
          this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);
          if (num <= 0) {
            delete this.bus.carnums[gd.guid];
            this.bus.cars[this.guid].splice(i, 1);
          }
          this.$set(this.bus.carnums, gd.guid, num);
          //   console.log(this.bus.cars);
          return;
        }
      }
    },
    //商店头部和内部的展开和收起
    cl() {
      this.jQuery(".a0v").css({ display: "none" });
      this.jQuery("#shopTop").css({ height: "108px" });
      this.jQuery(".shopAR").css({ display: "block" });
      this.jQuery(".xa").css({ display: "block" });
    },
    show() {
      this.jQuery(".shopAR").css({ display: "none" });
      this.jQuery(".xa").css({ display: "none" });
      this.jQuery("#shopTop").css({ position: "relative", height: "100%" });
      this.jQuery(".a0v").css({ display: "block" });
    },
    goodsMore() {
      if (this.jQuery(".a2v span").html() == "查看更多") {
        this.jQuery(".a2v span").html("点击收起");
        this.jQuery(".a2v i").css({ transform: "rotate(-180deg)" });
        this.jQuery(".af6").css({ height: "100%" });
      } else {
        this.jQuery(".a2v span").html("查看更多");
        this.jQuery(".af6").css({ height: "0" });
        this.jQuery(".a2v i").css({ transform: "rotate(0)" });
      }
    },
    //二级菜单展开和收起
    more(index) {
      this.jQuery(".lbox1")
        .not(this.jQuery(".lbox1").eq(index))
        .hide();
      this.jQuery(".lbox1")
        .eq(index)
        .toggle();
      this.jQuery(".lia")
        .eq(index)
        .css({ color: "#47b34f" });
      this.jQuery(".lia")
        .not(this.jQuery(".lia").eq(index))
        .css({ color: "#666" });
      if (
        this.jQuery(".lbox1")
          .eq(index)
          .css("display") == "none"
      ) {
        this.jQuery(".lia")
          .eq(index)
          .addClass("active");
        this.jQuery(".lia")
          .not(this.jQuery(".lia").eq(index))
          .removeClass("active");
      } else {
        this.jQuery(".lia")
          .eq(index)
          .removeClass("active");
      }
      this.jQuery(".lia")
        .not(this.jQuery(".lia").eq(index))
        .removeClass("active");
    },
    tog(){
      this.jQuery('.dhmore').toggle()
    }
  },
  //页面加载时请求一次数据
  mounted(text) {
    this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
      if (result.errcode == 0) {
        this.goods = result.data;
        this.ctgText = result.ctgText;
        this.count = result.count;
      }
    });
  }
};
</script>

