import Vue from 'vue'
import Router from 'vue-router'
import index from './components/index.vue'
import shop from './components/shop.vue'
import home from './components/home.vue'
import footer1 from './components/footer.vue'
import fenlie from './components/fenlie.vue'
import del from './components/del.vue'
import car from './components/car.vue'
import mine from './components/mine.vue'
import order from './components/order.vue'
import shopcar from './components/shopcar.vue'
import login from './components/login.vue'
import regist from './components/regist.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/footer',
      component: footer1,
    },
    {
      path: '/',
      redirect: '/home'//跳转路由
    },
    {
      path: '/shop/:guid/:title/:time/:many/:fg/:how/static/:src',
      component: shop,
      props: true
    },
    {
      path: '/home',
      components: {
        a: home,
        b: footer1,
      }
    },
    {
      path: '/mine',
      components: {
        a: mine,
        b: footer1,
      }
    },
    {
      path: '/order',
      components: {
        a: order,
        b: footer1,
      }
    },
    {
      path: '/shopcar',
      components: {
        a: shopcar,
        b: footer1,
      }
    },
    {
      path: '/fenlie',
      components: {
        a: fenlie,
        b: footer1,
      }
    },
    {
      path: '/shop/:guid/:time/:many/:fg/:how/static/:src/del/:title/:text/:zan/:price/static/:img',
      component: del,
      props: true
    },
    {
      path: '/regist',
      component: regist
    },
    {
      path: '/login',
      component: login
    },
  ]
})



