<template>
  <div id="login">
      <div class="logher">
          <router-link to="mine" class="logic"></router-link>
          <span class="logtext">京东登录</span>
      </div>
      <div class="logtab">
          <div class="loguser" @click="tobor">
              <span class="ustit1">账号密码登录</span>
          </div>
          <div class="loguser" @click="tobor1">
              <span class="ustit2">短信验证码登录</span>
          </div>
      </div>
      <div class="loginp">
        <el-form id="loginFrm1" :status-icon="true" :rules="rules" label-position="left" ref="form1"
        :model="form" v-show="!flage">
            <el-form-item label="账号" prop="username">
                <el-input v-model="form.username" name="username" placeholder="用户名/邮箱/已验证手机"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="form.password" name="password" placeholder="请输入密码"></el-input>
            </el-form-item>
            <div class="emp">&nbsp;</div>
            <el-form-item class="elbtn">
                <el-button @click.stop="login1()" type="info">登录</el-button>
            </el-form-item>
        </el-form>
        <el-form id="loginFrm" :status-icon="true" :rules="rules" label-position="left" ref="form"
        :model="form" v-show="flage">
            <el-form-item label="+86" prop="phone">
                <el-input v-model="form.phone" name="phone" placeholder="请输入手机号"></el-input>
            </el-form-item>
            <el-form-item label="验证码" prop="text">
                <el-input v-model="form.text" name="text" placeholder="请输入收到的验证码"></el-input>
            </el-form-item>
            <div class="emp">&nbsp;</div>
            <el-form-item class="elbtn">
                <el-button @click.stop="login()" type="info" class="elBtn2">登录</el-button>
            </el-form-item>
        </el-form>
        <div class="logbtton">一键登录</div>
        <div class="qunav">
            <span class="unrem">
                <i></i>
                <span>忘记密码</span>
            </span>
            <router-link to="regist" class="unrem unle">
                <i></i>
                <span>手机快速注册</span>
            </router-link>
        </div>
        <div class="quilog">
            <h4 class="qlgh4">其他登录方式</h4>
            <a class="qlga">
                <i></i>
            </a>
            <a class="qlga qlwx">
                <i></i>
            </a>
            <div class="qlgtip">
                <p>登录即代表您已同意
                    <a href="" class="tipa">京东隐私政策</a>
                </p>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flage: false,
      form: {
        username: "",
        password: "",
        phone: "",
        text: ""
      },
      rules: {
        username: [
          {
            validator: function(rule, value, callback) {
              if (value.trim() == "") {
                callback(new Error("请输入用户名"));
              } else {
                callback();
              }
            }
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              console.log(value);
              if (value.trim() == "") {
                callback(new Error("请输入密码"));
              } else {
                callback();
              }
            }
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              if (value.trim() == "") {
                callback(new Error("请输入手机号"));
              } else {
                callback();
              }
            }
          }
        ],
        text: [
          {
            validator: function(rule, value, callback) {
              console.log(value);
              if (value.trim() == "") {
                callback(new Error("请输入收到的验证码"));
              } else {
                callback();
              }
            }
          }
        ]
      }
    };
  },
  methods: {
    tobor() {
      this.jQuery(".ustit1").css({ borderBottomColor: "#f23030" });
      this.jQuery(".ustit2").css({ borderBottomColor: "#fff" });
      this.$data.flage = false;
    },
    tobor1() {
      this.jQuery(".ustit1").css({ borderBottomColor: "#fff" });
      this.jQuery(".ustit2").css({ borderBottomColor: "#f23030" });
      this.$data.flage = true;
    },
    login: function() {
      this.$refs.form.validate(function(valid) {
        if (valid) {
          //验证全部通过提交数据
          console.log(1);
        }
      });
    },
    login1: function() {
      this.$refs.form1.validate(function(valid) {
        if (valid) {
          //验证全部通过提交数据
          console.log(1);
        }
      });
    }
  },
  updated() {
    if (
      this.jQuery(".el-input__inner")
        .eq(1)
        .val() != "" &&
      this.jQuery(".el-input__inner")
        .eq(0)
        .val() != ""
    ) {
      this.jQuery(".el-button").removeClass("el-button--info");
      this.jQuery(".el-button").addClass("el-button--danger");
    } else {
      this.jQuery(".el-button").addClass("el-button--info");
      this.jQuery(".el-button").removeClass("el-button--danger");
    }
    if (
      this.jQuery(".el-input__inner")
        .eq(2)
        .val() != "" &&
      this.jQuery(".el-input__inner")
        .eq(3)
        .val() != ""
    ) {
      this.jQuery(".elBtn2").removeClass("el-button--info");
      this.jQuery(".elBtn2").addClass("el-button--danger");
    } else {
      this.jQuery(".elBtn2").addClass("el-button--info");
      this.jQuery(".elBtn2").removeClass("el-button--danger");
    }
  }
};
</script>

<style>
.logher {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  width: 100%;
  text-align: center;
}
.logic {
  width: 22px;
  height: 22px;
  overflow: hidden;
  position: absolute;
  background-image: url(/static/iconBg.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
  top: 12px;
  left: 12px;
  background-position: 0 -63px;
}
.logtab {
  line-height: 40px;
  font-size: 14px;
  border-bottom: 1px solid #d7d7d7;
  overflow: hidden;
}
.loguser {
  width: 50%;
  float: left;
  display: inline-block;
  text-align: center;
}
.ustit1 {
  border-bottom: 2px solid #f23030;
  padding: 11px 10px;
}
.ustit2 {
  padding: 11px 10px;
  border-bottom: 2px solid #fff;
}
.loginp {
  max-width: 750px;
  margin: 0 auto;
  padding: 0 20px 20px 20px;
}
.el-form-item {
  position: relative;
  width: 100%;
  height: 68px;
  line-height: 68px;
  overflow: hidden;
  padding: 0 5px;
  background: #fff;
  border-bottom: 1px solid #d7d7d7;
  margin-bottom: 0 !important;
}
.el-form-item__label {
  position: absolute;
  width: 60px;
  display: inline-block;
  text-align: left;
  font-size: 14px;
  padding: 0;
  margin: 0;
  line-height: 48px;
}
.el-input {
  width: 100%;
  padding: 0 30px 0 60px;
  font-size: 14px;
  display: inline-block;
}
.el-input__inner {
  border: none !important;
  outline: none !important;
  line-height: normal !important;
  padding: 0 !important;
}
.elbtn {
  color: #848689;
  height: 45px;
  line-height: 45px;
  border: 0;
  border-radius: 2px;
  padding: 0;
}
.emp {
  line-height: 30px;
  min-height: 15px;
}
.el-button {
  width: 100%;
  border: 0;
  height: 45px;
}
.el-button--info {
  background: #eee !important;
  color: #848689 !important;
}
.logbtton {
  color: #f23030;
  border: 1px solid #f23030;
  background: #fff;
  margin-top: 10px;
  height: 45px;
  line-height: 46px;
  text-align: center;
  border-radius: 2px;
}
.qunav {
  margin-top: 20px;
  text-align: center;
  overflow: hidden;
}
.unrem {
  float: left;
  color: #333;
  text-indent: 25px;
  line-height: 22px;
  display: inline-block;
  font-size: 12px;
}
.unrem i {
  width: 22px;
  height: 22px;
  overflow: hidden;
  position: absolute;
  background: url(/static/iconBg.png) no-repeat;
  background-size: 100% auto;
  background-position: 0 -23px;
  display: block;
}
.unle {
  float: right;
}
.unle i {
  background-position: 0 -42px;
}
.unle:hover {
  text-decoration: none;
  color: #333;
}
.quilog {
  margin-top: 105px;
  height: 85px;
  position: relative;
  padding: 24.5px 10px 0px 10px;
  border-top: 1px solid #d7d7d7;
  text-align: center;
  margin-bottom: 30px;
}
.qlgh4 {
  position: absolute;
  top: -19px;
  left: 31%;
  background: #fff;
  padding: 5px 15px;
  color: #bfbfbf;
  font-size: 14px;
  font-weight: normal;
}
.qlga {
  position: relative;
  width: 72px;
  color: #616161;
  margin: 0 10px;
  font-size: 14px;
  display: inline-block;
  text-align: center;
}
.qlga i {
  position: absolute;
  overflow: hidden;
  left: 25px;
  width: 22px;
  height: 22px;
  background: url(/static/iconBg.png) no-repeat;
  background-size: 100% auto;
  background-position: 0 -129px;
}
.qlwx i {
  background-position: 0 -155px;
}
.qlgtip {
  position: absolute;
  bottom: -30px;
  left: 0;
  width: 100%;
  height: 30px;
  font-size: 14px;
  line-height: 30px;
}
p {
  margin: 0;
}
.tipa {
  color: #333;
  text-decoration: underline;
}
</style>



