<script>
import "swiper/dist/css/swiper.min.css";
import index from "./index.vue";
import Swiper from "Swiper";
import infiniteScroll from "vue-infinite-scroll";
export default {
  directives: { infiniteScroll: infiniteScroll }, //滚动加载
  data() {
    return {
      imgs: [
        {
          src: "/static/cs.png",
          tit: "超市生鲜"
        },
        {
          src: "/static/sg.png",
          tit: "新鲜水果"
        },
        {
