          tit: "美食烘焙"
        },
        {
          src: "/static/xh.png",
          tit: "鲜花蛋糕"
        },
        {
          src: "/static/yy.png",
          tit: "医药健康"
        },
        {
          src: "/static/sc.png",
          tit: "新人专享"
        },
        {
          src: "/static/vip.png",
          tit: "VIP会员"
        },
        {
          src: "/static/wt.png",
          tit: "帮助中心"
        },
        {
          src: "/static/lw.png",
          tit: "分享有礼"
        },
        {
          src: "/static/qd.png",
          tit: "签到"
        }
      ],
      busy: false,
      shops: [],
      b: false
    };
  },
  created() {
    var $ = this.jQuery;
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shops = result.data; //获取数据
    });
    $(window).scroll(function() {
      var top = $(this).scrollTop();
      var al = top / 108;
      if (al > 1) {
        return;
      }
      $(".search").css("background", "rgba(75,201,85," + (1 - al) + ")");
      $(".topnav").css("background", "rgba(75,201,85," + al + ")");
      if (top <= 10) {
        $(".topnav .right").css({
          marginRight: "-30px",
          transition: "1s"
        });
      } else {
        $(".topnav .right").css({
          marginRight: "10px",
          transition: "1s"
        });
      }
    });
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      loop: true,
      autoplay: {
        delay: 3000,
        stopOnLastSlide: false,
        disableOnInteraction: false
      }, //可选选项，自动滑动
      pagination: {
        el: ".swiper-pagination"
      }
    });
  },
  methods: {
    loadMore() {
      this.busy = true;
      this.jQuery.get(this.apiURL + "/shops", result => {
        this.shops = this.shops.concat(result.data); //获取数据
        this.busy = false;
      });
    },
    sermore() {
      this.jQuery("#ser").show();
    },
    more(index) {
      this.jQuery(".down")
        .eq(index)
        .css({ display: "none" });
      this.jQuery(".shang")
        .eq(index)
        .css({ display: "block" });
      this.jQuery(".adv")
        .eq(index)
        .css({ height: "100%" });
    },
    shou(index) {
      this.jQuery(".down")
        .eq(index)
        .css({ display: "block" });
      this.jQuery(".shang")
        .eq(index)
        .css({ display: "none" });
      this.jQuery(".adv")
        .eq(index)
        .css({ height: "75px" });
    },
    Back() {
      this.jQuery("#ser").hide();
    }
  },
  components: {
    // index,
    // footer1
  }
};
</script>

