<template>
  <div id="order">
    <div v-for="order in orders" :key="order.guid" class="ordbox">
      <div class="ordname">
        <div class="ordsna">{{order.sname}}<span>&#xe600;</span></div>
        <div class="state">已完成</div>
      </div>
      <div class="ordgoods">
        <div class="gopic">
          <img :src="child.pic" alt="" v-for="child in order.children" :key="child.guid">
        </div>
        <div class="gomany">
          <div class="gomu">&yen;{{order.pr}}</div>
          <div class="gomy">共{{order.children.length}}件</div>
          <div class="gofc">达达专送</div>
        </div>
      </div>
      <div class="gotime">
        <div class="got">{{order.date}} {{order.time}}</div>
        <div class="goage">再次购买</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
      return{
         orders: []
      }
  },
  created() {
    this.jQuery.get(this.apiURL + "/order", result => {
      if (result.errcode == 0) {
        this.orders = result.data;
      }
    });
  }
}
</script>

<style>
#order {
  padding: 0 0 99px 0;
  text-align: center;
  background-color: #f4f4f4;
  overflow: hidden;
}
.ordbox{
  background: #fff;
  margin-bottom: 20px;
  overflow: hidden;
}
.ordname{
  width: 100%;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
}
.ordsna{
  font-size: 15px;
  float: left;
  padding: 8px;
  font-family: 'iconfont';
  font-weight: 600;
}
.ordsna span{
  color: #ddd;
  font-weight: 600;
}
.state{
  float: right;
  padding: 8px 12px;
  font-size: 15px;
}
.ordgoods{
  margin-left: 12px;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
.gopic{
  padding: 10px 0;
  overflow: hidden;
  float: left;
  max-width: 80%;
}
.gopic img{
  width: 53px;
  height: 53px;
  float: left;
}
.gomany{
  padding: 8px 0;
  float: right;
  padding-right: 12px;
}
.gomu{
  color: red
}
.gomy{
  font-size: 12px;
  text-align: right;
}
.gofc{
  font-size: 12px;
  padding:0 3px;
  border: 1px solid #00bcd4;
  border-radius: 3px;
  color: #00bcd4;
}
.gotime{
  padding:8px 12px;
  overflow: hidden;
}
.got{
  float: left;
  color: #000;
  font-size: 14px;
  font-weight: 500;
}
.goage{
  font-size: 14px;
  float: right;
  border: 1px solid #ddd;
  padding: 3px 12px;
  border-radius: 3px
}
</style>


