<template>
   <div id="del">
      <div class="swiper-container" id="swp1" style="z-index:0">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><img :src="'/static/'+img" alt=""></div>
          <div class="swiper-slide"><img src="/static/img71.jpg" alt=""></div>
          <div class="swiper-slide"><img src="/static/img72.jpg" alt=""></div>
          <div class="swiper-slide"><img src="/static/img73.jpg" alt=""></div>
          <div class="swiper-slide"><img src="/static/img74.jpg" alt=""></div>
        </div>
        <div class="swiper-pagination swpA" style="bottom:10px"></div>
      </div>
       <div class="hr_sha"></div>
       <div class="delname">
           <div class="deltext">{{text}}</div>
       </div>
       <div class="delmuch">
           <div class="delej">
               <p class="delpir">
                   <span>&yen;</span>{{price}}
               </p>
               <span class="delsure">加入购物车</span>
           </div>
       </div>
       <div class="delna">
         <div class="delnm">
           <router-link :to="'/shop/'+guid+'/'+title+'/'+time+'/'+many+'/'+fg+'/'+how+'/static/'+src" class="delnma">
             <i></i>
             <span>{{title}}</span>
           </router-link>
         </div>
         <div class="delfac">
            <a>
              <i></i>
              <span>联系商家</span>
            </a>
          </div>
       </div>
       <div class="delpl">
         <div class="plmore">
           <span class="plsp">商品评价
             <span>(共3人评价)</span>
           </span>
           <div class="plh2">
             <div class="plh3">
               <span class="plh4" :style="'width:'+zan+'%'">
                 <span class="plh5"></span>
                 <span class="plh6"></span>
               </span>
             </div>
             <span class="plh7">好评{{zan}}%</span>
           </div>
         </div>
       </div>
       <div class="delgp">
         <span class="gptit">查看图文详情</span>
         <span class="circle">></span>
       </div>
       <div style="padding-bottom:30px;"></div>
       <div class="daohang" @click="tog">&#xe69b;
        <div class="dhmore">
          <router-link to="/home" class="dha"><span class="dhico">&#xe62a;</span><span>首页</span></router-link>
          <router-link to="/fenlie" class="dha"><span class="dhico">&#xe6bb;</span><span>分类</span></router-link>
          <router-link to="" class="dha"><span class="dhico">&#xe602;</span><span>购物车</span></router-link>
          <router-link to="" class="dha"><span class="dhico">&#xe649;</span><span>订单</span></router-link>
          <router-link to="" class="dha"><span class="dhico">&#xe69a;</span><span>我的</span></router-link>
          <i class="sanj"></i>
        </div>
       </div>
       <div class="cover"></div>
       <car :shopid="guid"></car>
   </div>
</template>
<script>
import "swiper/dist/css/swiper.min.css";
import car from "./car";
import Swiper from "Swiper";
export default {
  props: [
    "text",
    "title",
    "zan",
    "price",
    "img",
    "guid",
    "time",
    "many",
    "fg",
    "how",
    "src"
  ],
  data() {
    return {};
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        type: "fraction"
      }
    });
  },
  methods: {
    tog() {
      this.jQuery(".dhmore").toggle();
    }
  },
  created() {
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []);
    }
  },
  components: {
    car
  }
};
</script>

<style>
#del {
  margin-bottom: 45px;
  background-color: #f4f4f4;
}
.swiper-container {
  width: 100%;
}
.swiper-slide img {
  width: 100%;
}
.swpA {
  width: 50px;
  height: 24px;
  left: 80%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  text-align: center;
  color: #ffffff;
  font-size: 20px;
}
.hr_sha {
  width: 100%;
  height: 8px;
  overflow: hidden;
  background: -webkit-linear-gradient(
    left,
    #ffffff 0%,
    #d4d3d3 49%,
    #d4d3d3 50%,
    #ffffff 100%
  );
  box-shadow: inset 0px 8px 5px rgba(255, 255, 255, 0.8);
}
.delname {
  background-color: #fff;
  overflow: hidden;
}
.deltext {
  text-align: center;
  font-size: 16px;
  padding: 0 20px;
  margin: 10px 0 2px;
  color: #333;
  line-height: 22px;
  text-overflow: ellipsis;
  overflow: hidden;
  max-height: 44px;
}
.delmuch {
  background: #fff;
  padding: 0 0 0 10px;
}
.delej {
  min-height: 49px;
  line-height: 49px;
  position: relative;
  font-size: 0;
}
.delpir {
  display: inline-block;
  font-size: 30px;
  color: #ff3434;
  margin-right: 5px;
  margin-bottom: 0;
}
.delpir span {
  font-size: 20px;
  padding: 5px;
  font-weight: 500;
}
.delsure {
  background-color: #47b34f;
  position: absolute;
  right: 10px;
  top: 8px;
  display: inline-block;
  padding: 0 10px;
  font-size: 13px;
  color: #fff;
  height: 33px;
  line-height: 33px;
  border-radius: 2px;
}
.delna {
  background: #fff;
  height: 50px;
  font-size: 13px;
  color: #333;
  margin-top: 10px;
}
.delnm {
  position: relative;
  float: left;
  height: 50px;
  line-height: 50px;
  width: 65%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.delnma {
  display: block;
  height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 50px;
  text-align: left;
  padding-left: 10px;
}
.delnma i {
  position: absolute;
  top: 13px;
  left: 10px;
  height: 20px;
  width: 20px;
  background: url(/static/icon1.png) no-repeat;
  background-size: 200px 150px;
  background-position: -44px 4px;
}
.delnma span {
  padding-left: 25px;
  height: 24px;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #333333;
}
.delnm::after {
  content: "";
  width: 1px;
  height: 20px;
  border-right: 1px solid #d9d9d9;
  position: absolute;
  top: 15px;
  right: 0;
}
.delfac {
  position: relative;
  float: left;
  height: 50px;
  line-height: 50px;
  width: 35%;
}
.delfac a {
  display: block;
  height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 50px;
  text-align: right;
  padding-right: 15px;
}
.delfac i {
  height: 20px;
  width: 20px;
  background: url(/static/icon1.png) no-repeat;
  background-size: 200px 150px;
  background-position: -22px 4px;
  display: inline-block;
  vertical-align: top;
  margin-top: 13px;
}
.delfac span {
  display: inline-block;
  vertical-align: top;
  color: #333;
}
.delpl {
  background: #fff;
  padding: 0 10px;
  font-size: 13px;
  color: #333333;
  margin: 0 0 10px 0;
  position: relative;
  line-height: 50px;
}
.delmore {
  position: relative;
}
.delmore::after {
  height: 1px;
  content: "";
  width: 100%;
  border-top: 1px solid #e8e8e8;
  position: absolute;
  top: 0;
  right: 0;
}
.plsp span {
  color: #999999;
  font-size: 12px;
  padding-left: 4px;
}
.plh2 {
  margin-top: 10px;
  padding-bottom: 20px;
  position: relative;
}
.plh3 {
  position: relative;
  margin-right: 70px;
  background: #e8e8e8;
  height: 3px;
  border-radius: 2px;
}
.plh4 {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  background: #47b34f;
  height: 3px;
  border-radius: 2px;
}
.plh5 {
  display: inline-block;
  position: absolute;
  right: -4px;
  top: -3px;
  width: 8px;
  height: 8px;
  background-color: #47b34f;
  border-radius: 4px;
}
.plh6 {
  display: inline-block;
  position: absolute;
  right: -2px;
  top: -1px;
  width: 4px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 2px;
}
.plh7 {
  position: absolute;
  right: 0;
  top: -24px;
  font-size: 12px;
  color: #21a21b;
}
.delgp {
  padding: 22px 0;
  text-align: center;
  background: #f4f4f4;
  position: relative;
}
.gptit {
  color: #333;
  font-size: 12px;
}
.circle {
  margin: 3px 0 0 4px;
  display: inline-block;
  background-color: #47b34f;
  border-radius: 50%;
  width: 16px;
  height: 18px;
  text-align: center;
  line-height: 16px;
  color: #fff;
}
.daohang {
  position: fixed;
  z-index: 1;
  right: 0;
  bottom: 115px;
  width: 36px;
  height: 36px;
  border: 1px solid #757575;
  border-radius: 4px 0 0 4px;
  background: rgba(0, 0, 0, 0.8);
  font-family: "iconfont";
  color: #fff;
  text-align: center;
  line-height: 36px;
  font-size: 33px;
}
.dhmore {
  position: absolute;
  right: -1px;
  bottom: 42px;
  width: 95px;
  border: 1px solid #757575;
  background: rgba(0, 0, 0, 0.8);
  font-size: 14px;
  display: none;
}
.dha {
  display: block;
  color: #fff;
  border-bottom: 1px solid #4b4b4b;
  line-height: 35px;
  height: 35px;
}
.dha span {
  display: inline-block;
  float: left;
}
.dhico {
  width: 40%;
  font-size: 16px;
}
.sanj {
  position: absolute;
  bottom: -12px;
  right: 10px;
  width: 0;
  height: 0;
  z-index: 1;
  border-width: 6px;
  border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
  border-style: solid;
}
.dha:hover {
  text-decoration: none;
  color: #fff;
}
</style>



