<template>
  <div>
    <div class="fen-box">
      <div class="fen-search">
        <span>&#xe613;</span>
        <div>搜索附近的商家、商品</div>
      </div>
    </div>
     <div class="fenlie">
       <div class="fen-left">
         <ul>
           <li class="kinds" v-for="(list,index) in lists" :key="list.guid" @click="showGoods(list.text,index)">
             <router-link to="" class="rots">{{list.text}}</router-link>
           </li>
         </ul>
       </div>
       <div class="fen-right">
         <ul>
           <li v-for="cls in clses" :key="cls.guid">
             <p class="fen-tit">{{cls.title}}</p>
             <ul class="fen-mores">
               <li class="fen-more" v-for="child in cls.children" :key="child.guid">
                 <div><img :src="child.img" alt=""></div>
                 <p>{{child.text}}</p>
               </li>
             </ul>
           </li>
         </ul>
       </div>
     </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      clses: [],
      lists: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/lists", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        this.lists = result.data;
      }
    });
  },
  methods: {
    showGoods(text, index) {
      this.jQuery.get(this.apiURL + "/fenlie", { clstext: text }, result => {
        if (result.errcode == 0) {
          this.clses = result.data;
        }
      });
      this.jQuery(".kinds")
        .eq(index)
        .addClass("border")
        .siblings()
        .removeClass("border");
    }
  },
  mounted(text) {
    this.jQuery.get(this.apiURL + "/fenlie", { clstext: text }, result => {
      if (result.errcode == 0) {
        this.clses = result.data;
      }
    });
  }
};
</script>

<style>
html,
body {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "iconfont";
}
.fenlie ul {
  list-style: none;
  margin-top: 10px;
}
.fen-left ul {
  margin: 0;
}
.fen-box {
  height: 44px;
  color: #555;
  font-size: 14px;
  text-align: center;
  line-height: 44px;
  background-color: #fff;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
}
.fen-search {
  position: absolute;
  left: 8px;
  top: 8px;
  right: 8px;
  background: #fff;
  border-radius: 2px;
  border: 1px solid #e6e6e6;
  height: 28px;
  padding: 0 5px 0 20px;
}
.fen-search span {
  position: absolute;
  top: 0;
  left: 0;
  height: 28px;
  line-height: 28px;
  width: 30px;
  text-align: center;
}
.fen-search div {
  height: 28px;
  line-height: 28px;
  position: absolute;
  left: 34px;
}
.fenlie {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 44px;
}
.fen-left {
  width: 28.125%;
  background: #f8f8f8;
  /* height: 100%; */
}
.fen-right {
  width: 71.875%;
  overflow: auto;
  margin-bottom: 80px;
}
.kinds {
  width: 100%;
  height: 44px;
  line-height: 44px;
  padding-left: 14px;
  background: #f8f8f8;
  font-size: 14px;
  border-bottom: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
}
.border {
  padding-left: 11px;
  color: #47b34f;
  background-color: #fff;
  border-left: 3px solid #47b34f;
  border-right-color: #fff;
}
.green {
  border-left: 2px solid #47b34f;
  color: #47b34f;
}
.fen-tit {
  height: 19px;
  margin-top: 19px;
  margin-bottom: 9px;
  margin-left: 7%;
  line-height: 19px;
  font-size: 13px;
  color: #666666;
}
.fen-mores {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
}
.fen-more {
  width: 33%;
}
.fen-more div {
  height: 80px;
  width: 80%;
  margin: 0 auto;
}
.fen-more div img {
  width: 100%;
}
.fen-more p {
  text-align: center;
  font-size: 12px;
  color: #7d7d7d;
}
.rots:hover {
  text-decoration: none;
  color: #39ac69;
}
.rots {
  color: #555;
  display: inline-block;
  width: 100%;
}
</style>



