<template>
  <div>
      <ul>
        <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
            <li v-for="(shop) in shops" :key="shop.guid">
              
              <h2><router-link :to="'/shop/'+shop.guid+'/'+shop.title">{{shop.title}}</router-link></h2>
              <p>{{shop.star}}</p>
             </li>
        </div>     
      </ul>
  </div>
</template>
 
<script>
import infiniteScroll from "vue-infinite-scroll";

export default {
  directives: { infiniteScroll:infiniteScroll },//滚动加载
  data() {
    return {
      busy: false,
      shops: []
    };
  },
  methods: {
    loadMore() {
      this.busy = true;
      this.jQuery.get(this.apiURL + "/shops", result => {
        this.shops =this.shops.concat(result.data); //获取数据
        this.busy = false;
      });
    }
  },
  created() {
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shops = result.data; //获取数据
    });
  }
};
</script>



