<template>
  <div id="shopcar">
    <div class="goodbox">
      <div class="mymap">
        <div class="mapdel">弓箭坊40号<i class="mapdet">(当前位置)</i></div>
      </div>
      <div class="spgoods" v-for="shopcar in shopcars" :key="shopcar.guid"> 
        <div class="godBox">
          <span class="spsname">{{shopcar.sname}}</span>
          <div class="sppic">
            <div class="spthi" v-for="child in shopcar.children" :key="child.guid">
              <img :src="child.pic" alt="">
              <em>&yen;{{child.pr}}</em>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shopcars: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/shopcar", result => {
      if (result.errcode == 0) {
        this.shopcars = result.data;
      }
    });
  }
};
</script>

<style>
.goodbox {
  padding: 0 0 99px 0;
  text-align: center;
  background-color: #f4f4f4;
  overflow: hidden;
}
.mymap {
  display: inline-block;
  vertical-align: top;
  padding: 6px 7px;
  border-radius: 32px;
  background-color: #d1e7d3;
  margin: 15px 0 10px 0;
}
.mapdel {
  display: inline-block;
  vertical-align: top;
  line-height: 22px;
  padding: 0 14px 0 31px;
  border-radius: 22px;
  font-size: 14px;
  color: #fff;
  background: #47b34f url(/static/loc2.png) 13px center no-repeat;
  background-size: 10px 12px;
}
.mapdet {
  font-size: 12px;
  position: relative;
  top: -1px;
  padding-left: 2px;
  font-style: normal;
}
.spgoods {
  position: relative;
  margin: 0 6px 7px 6px;
  text-align: left;
  height: 141px;
  overflow: hidden;
  padding: 3px 4% 10px 1.6%;
  background: url(/static/gc.png) 0 0 no-repeat;
  background-size: 100% 154px;
  z-index: 0;
}
.godBox {
  overflow: hidden;
  padding-left: 2.4%;
}
.spsname {
  display: block;
  position: relative;
  z-index: 3;
  line-height: 44px;
  height: 147px;
  margin-bottom: -103px;
  color: #333;
  font-size: 15px;
  background: url(/static/m.png) right 17px no-repeat;
  background-size: 7px 11px;
}
.sppic {
  position: relative;
  height: 97px;
  padding: 1px 0 0 0;
}
.spthi {
  float: left;
  display: inline-block;
  height: 83px;
  vertical-align: top;
  width: 53px;
  margin-top: 14px;
  margin-right: 10px;;
  text-align: center;
  font-size: 12px;
  line-height: 30px;
  color: #333;
}
.spthi img {
  vertical-align: top;
  width: 53px;
  height: 53px;
  border-radius: 5px;
}
em{
  font-style: normal;
}
</style>


