<template>
  <div id="regist">
    <div class="logher">
      <router-link to="login" class="logic"></router-link>
      <span class="logtext">京东注册</span>
    </div>
    <div class="reginp">
      <el-form id="loginFrm" :status-icon="true" :rules="rules" label-position="left" ref="form"
      :model="form">
        <el-form-item label="账号" prop="username">
            <el-input v-model="form.username" name="username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="form.password" name="password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="password2">
            <el-input type="password" v-model="form.password2" placeholder="请输入确认密码"></el-input>
        </el-form-item>
         <el-form-item label="邮箱" prop="email">
            <el-input v-model="form.email" name="email" placeholder="请输入邮箱"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="phone">
            <el-input v-model="form.phone" name="phone" placeholder="请输入手机号"></el-input>
        </el-form-item>
        <div class="emp">&nbsp;</div>
        <el-form-item class="elbtn">
            <el-button @click="login()" type="info">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="regcover"></div>
    <div class="agreeBody">
      <div class="agreeTit">注册协议及隐私政策</div>
      <div class="agreeLine"></div>
      <div class="agreeCont">
        <div>在您注册成为京东用户的过程中，您需要完成我们的注册流程并通过点击同意的形式在线签署以下协议，
          <span class="imptip">请您务必仔细阅读、充分理解协议中的条款内容后再点击同意（尤其是以粗体并下划线标识的条款，因为这些条款可能会明确您应履行的义务或对您的权利有所限制）：</span>
        </div>
        <div class="linkTop">《京东用户注册协议》</div>
        <div class="linkCont">《用户隐私政策》</div>
        <div class="linkBot">《订单共享与安全》</div>
        <div>
          <span class="imptip">【请您注意】如果您不同意上述协议或其中任何条款约定，请您停止注册。您停止注册后将仅可以浏览我们的商品信息但无法享受我们的产品或服务。如您按照注册流程提示填写信息、阅读并点击同意上述协议且完成全部注册流程后，即表示您已充分阅读、理解并接受协议的全部内容；并表明您也同意京东可以依据以上的隐私政策内容来处理您的个人信息。</span>
          如您对以上协议内容有任何疑问，您可随时与京东客服联系。
        </div>
      </div>
      <div class="agreeBtnW">
        <div class="agreeNot">
          <span>点击同意即表示您已阅读并同意
            <a class="aspa">《京东用户注册协议》</a>与
            <a class="aspa">《京东隐私政策》</a>并将您的订单信息共享给为完成此订单所必须的第三方合作方。关于
            <a class="aspa">《订单共享与安全》</a>
          </span>
        </div>
        <div class="agreeBtns">
          <router-link to="login" class="agreeBtncan">不同意</router-link>
          <a class="agreeBtncan1" style="color:#fff" @click="Ok">同意</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var _this = this;
    var chkPwd = function(value, callback) {
      if (value.trim() == "") {
        callback(new Error("请输入确认密码"));
      } else if (value == _this.form.password) {
        callback();
      } else {
        callback(new Error("两次密码不一致"));
      }
    };
    return {
      form: {
        username: "",
        password: "",
        password2: "",
        email: "",
        phone: ""
      },
      rules: {
        username: [
          {
            validator: function(rule, value, callback) {
              var usernamereg = /^[a-z]\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error("请输入用户名"));
              } else if (!usernamereg.test(value)) {
                callback(new Error("请输入2-18个字符，以小写字母开始"));
              } else {
                callback();
              }
            }
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              var usernamereg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
              if (value.trim() == "") {
                callback(new Error("请输入手机号"));
              } else if (!usernamereg.test(value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            }
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error("请输入密码"));
              } else if (len < 6 || len > 18) {
                callback(new Error("密码长度6到18个字符"));
              } else {
                _this.$refs.form.validateField("password2");
                callback();
              }
            }
          }
        ],
        password2: [
          {
            validator: function(rule, value, callback) {
              chkPwd(value, callback);
            }
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              var emailnamereg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
              if (value.trim() == "") {
                callback(new Error("请输入邮箱"));
              } else if (!emailnamereg.test(value)) {
                callback(new Error("邮箱格式不合法"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    Ok() {
      this.jQuery(".regcover").toggle();
      this.jQuery(".agreeBody").toggle();
    },
    login() {
      this.$refs.form.validate(function(valid) {
        if (valid) {
          //验证全部通过提交数据
         console.log(1)
        }
      });
    }
  }
};
</script>

<style>
.regcover {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
}
.agreeBody {
  position: absolute;
  bottom: 0;
  padding: 0 10px 0 10px;
  background: #f8f8f8;
  width: 100%;
  overflow: hidden;
  border-radius: 3px;
}
.agreeTit {
  width: 100%;
  font-weight: bold;
  font-size: 18px;
  padding: 12.5px 0;
  text-align: center;
  color: #000;
}
.agreeLine {
  border-top: 1px solid #d7d7d7;
  width: 107%;
  float: left;
  margin-left: -10px;
}
.agreeCont {
  width: 100%;
  height: 190px;
  font-size: 13px;
  padding-top: 12.5px;
  text-align: left;
  overflow-y: scroll;
  overflow-x: hidden;
}
.imptip {
  font-weight: bold;
  text-decoration: underline;
}
.linkTop {
  font-size: 13px;
  line-height: 18px;
  margin-top: 10px;
}
.linkCont {
  font-size: 13px;
  line-height: 18px;
}
.linkBot {
  font-size: 13px;
  line-height: 18px;
  margin-bottom: 10px;
}
.agreeBtnW {
  width: 100%;
  background: #f8f8f8;
}
.agreeNot {
  text-align: left;
  font-size: 13px;
  padding: 10px 5px 0 0;
}
.agreeNot span {
  font-weight: bold;
  line-height: 13px;
}
.agreeNot span .aspa {
  text-decoration: underline;
  color: #e93b3d;
}
.agreeBtns {
  height: 55px;
  font-size: 16px;
  text-align: center;
}
.agreeBtncan {
  color: #000;
  float: left;
  margin-left: 10px;
  background: #fff;
  border: 1px solid #d7d7d7;
  display: inline-block;
  text-align: center;
  width: 40%;
  line-height: 39px;
  margin: 8px 0;
  border-radius: 3px;
}
.agreeBtncan1 {
  color: #fff;
  float: right;
  margin-left: 10px;
  background: #e93b3d;
  border: 1px solid #e93b3d;
  display: inline-block;
  text-align: center;
  width: 40%;
  line-height: 39px;
  margin: 8px 0;
  border-radius: 3px;
}
.logher {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  width: 100%;
  text-align: center;
}
.logic {
  width: 22px;
  height: 22px;
  overflow: hidden;
  position: absolute;
  background-image: url(/static/iconBg.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
  top: 12px;
  left: 12px;
  background-position: 0 -63px;
}
.reginp {
  margin: 0 auto;
  padding: 20px 20px 20px 20px;
}
.el-form-item {
  position: relative;
  width: 100%;
  height: 68px;
  line-height: 68px;
  overflow: hidden;
  padding: 0 5px;
  background: #fff;
  border-bottom: 1px solid #d7d7d7;
  margin-bottom: 0;
}
.el-form-item__label {
  position: absolute;
  width: 60px;
  display: inline-block;
  text-align: left;
  font-size: 12px;
  padding: 0;
  margin: 0;
  line-height: 48px;
}
.el-input {
  width: 100%;
  padding: 0 30px 0 60px;
  font-size: 14px;
  display: inline-block;
}
.el-input__inner {
  border: none;
  outline: none;
  line-height: normal;
  padding: 0;
}
.elbtn {
  color: #848689;
  height: 45px;
  line-height: 45px;
  border: 0;
  border-radius: 2px;
  padding: 0;
}
.emp {
  line-height: 30px;
  min-height: 15px;
}
.el-button {
  width: 100%;
  border: 0;
  height: 45px;
}
.el-button--info {
  background: #eee;
  color: #848689;
}
</style>


