<template>
    <div id="car">
      <div class="balance1" v-show="bus.cars[this.shopid].length<=0">
        <div class="carjs1">去结算</div>
        <div class="carri1">购物车是空的</div>  
        <div class="caremp" v-show="bus.cars[this.shopid].length<=0"></div>
      </div> 
      <div class="payment" v-show="bus.cars[this.shopid].length>0">
        <div class="shoplogo" @click="showDtl">
          <p class="tm">{{totalnum}}</p>
        </div>
        <div class="table">
          <div class="all">
            <div class='checkboxall'> 
              <input type='checkbox' id='checkboxall' v-model="allchecked" name='checkboox1[]'>
              <label for='checkboxall' class="labelbox1" @click="checkedbox1()"></label> 
            </div>
            <span>全选(已选{{bus.cars[shopid].length}}件)</span>
            <div class="empty" @click="emptyCar">清空购物车</div>
          </div>
          <div class="shoptbox">
            <div v-for="(car,index) in bus.cars[shopid]" :key="car.guid" class="shopping">
              <div class="checkbox">
                <input type="checkbox" v-model="checked" :value="car.guid" :id="'check'+index" name='checkboox[]'>
                <label :for="'check'+index"  @click="checkbox(car.guid)"></label>
              </div> 
              <div class="shopic"><img :src="car.img" alt=""></div>
              <div class="shopname">{{car.text}}</div>
              <div class="shopmuch">&yen;{{car.price}}</div>
              <div class="numbers">
                  <span class="button1" @click="subCar(car)"></span>
                  {{car.num}}
                  <span class="button2" @click="addCar(car)"></span>
              </div>
              <!-- <div>&yen;{{(car.num*car.price) | currency}}</div> -->
            </div>
          </div>
        </div>
        <div class="total">&yen;<b>{{total | currency}}</b></div>
        <div class="pay">去结算</div>
      </div>
    </div>
</template>

<script>
export default {
  props: ["shopid"],
  data() {
    return {
      checked: [],
      checked1: [],
      allchecked:[],
      n: 0,
      m: 0
    };
  },
  computed: {
    total() {
      //总价
      var sum = 0;
      var shopid = this.bus.cars[this.shopid];
      for (var i = 0; i < shopid.length; i++) {
        if (this.checked.indexOf(shopid[i].guid) == -1) {
          continue;
        }
        sum += shopid[i].price * shopid[i].num;
      }
      return sum;
    },
    //添加商品的总量
    totalnum() {
      var sum = 0;
      for (let i = 0; i < this.bus.cars[this.shopid].length; i++) {
        sum += this.bus.cars[this.shopid][i].num;
      }
      return sum;
    }
  },
  methods: {
    //全选
    checkedbox1() {
      for (let i = 0; i < this.bus.cars[this.shopid].length; i++) {
        var gid = this.bus.cars[this.shopid][i].guid;
        if (this.m % 2 == 0) {
          if (this.checked.indexOf(gid) == -1) {
            this.checked.push(gid);
          }
        } else {
          this.checked = [];
        }
      }
      this.m++;
    },
    checkbox(id) {
      let index=this.checked.indexOf(id)
      if(index>=0){
        this.checked.splice(index,1)
      }else{
        this.checked.push(id)
      }
      if(this.checked.length== this.bus.cars[this.shopid].length){
        this.allchecked=true
        this.m=1
      }else{
        this.allchecked=false
        this.m=0
      }       
    },
    emptyCar() {
      this.jQuery(".shoplogo").css({ top: "-5px" });
      this.jQuery(".total").css({ left: "60px" });
      this.jQuery(".table").slideToggle();
      this.jQuery(".cover").toggle();
      //清空购物车
      this.$set(this.bus.cars, this.shopid, []);
      this.$set(this.bus, "carnums", {});
    },
    addCar(gd) {
      //添加商品
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        //判断是否存在相同的商品
        if (car[i].guid == gd.guid) {
          if (gd.num >= gd.store) {
            gd.num = gd.store - 1;
          }
          var num = ++this.bus.cars[this.shopid][i].num;
          this.bus.cars[this.shopid].splice(
            i,
            1,
            this.bus.cars[this.shopid][i]
          );
          this.$set(this.bus.carnums, gd.guid, num);
          console.log(this.bus.cars);
          return;
        }
      }
      if (gd.store == 0) {
        return;
      }
      gd.num = 1;
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.shopid].push(gd); //添加商品
      this.$set(this.bus.carnums, gd.guid, 1);
      console.log(this.bus.cars);
    },
    subCar(gd) {
      //减商品
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        //判断是否存在相同的商品
        if (car[i].guid == gd.guid) {
          var num = --this.bus.cars[this.shopid][i].num;
          this.bus.cars[this.shopid].splice(
            i,
            1,
            this.bus.cars[this.shopid][i]
          );
          if (num <= 0) {
            delete this.bus.carnums[gd.guid];
            this.bus.cars[this.shopid].splice(i, 1);
          }
          if (this.bus.cars[this.shopid].length == 0) {
            this.jQuery(".cover").css({ display: "none" });
            this.jQuery(".shoplogo").css({ top: "-5px" });
            this.jQuery(".total").css({ left: "60px" });
            this.jQuery(".table").css({ display: "none" });
          }
          this.$set(this.bus.carnums, gd.guid, num);
          return;
        }
      }
    },
    showDtl() {
      this.jQuery(".table").slideToggle();
      this.jQuery(".cover").toggle();
      if (this.jQuery(".shoplogo").css("top") == "-5px") {
        this.jQuery(".shoplogo").css({ top: "-52px" });
        this.jQuery(".total").css({ left: "0" });
      } else if (this.jQuery(".shoplogo").css("top") == "-52px") {
        this.jQuery(".shoplogo").css({ top: "-5px" });
        this.jQuery(".total").css({ left: "60px" });
      }
    }
  },
  filters: {
    currency(value) {
      value = Number(value);
      return value.toFixed(2);
    }
  }
};
</script>


<style>
* {
  margin: 0;
  padding: 0;
  font-family: "iconfont";
}
#car {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: white;
  z-index: 10;
  border-top: 1px solid #e5e5e5;
}
.shoplogo {
  height: 50px;
  width: 80px;
  transition: 0.5s;
  float: left;
  position: absolute;
  background: url(/static/cart.png) no-repeat 10px -75px;
  background-size: 50px 125px;
  top: -5px;
  z-index: 2;
}
.pay {
  height: 50px;
  width: 115px;
  color: white;
  font-size: 18px;
  background: #47b34f;
  line-height: 50px;
  text-align: center;
  float: right;
  font-size: 16px;
  position: relative;
  z-index: 2;
}
#car > p {
  margin-left: 15px;
  color: #bebebe;
  width: 150px;
  font-size: 16px;
  float: left;
  line-height: 50px;
}
.total {
  width: 100px;
  float: left;
  margin-top: 10px;
  font-size: 20px;
  color: red;
  padding-left: 15px;
  position: absolute;
  left: 60px;
  transition: 0.5s;
  z-index: 2;
}
.table {
  width: 100%;
  display: none;
  max-height: 400px;
  overflow: auto;
  overflow-x: hidden;
}
.numbers {
  padding-top: 20px;
  position: absolute;
  right: 55px;
  bottom: 14px;
}
.empty {
  text-align: right;
  color: #666;
  font-size: 14px;
  float: right;
  margin-right: 10px;
  margin-top: 5px;
}
.shopic {
  height: 62px;
  width: 62px;
  position: absolute;
  padding: 5px 0;
}
.shopic img {
  height: 52px;
  width: 52px;
  margin-top: 10px;
  left: 0;
}
.button1 {
  position: absolute;
  z-index: 0;
  top: 11px;
  width: 49px;
  height: 52px;
  background-image: url(/static/op.png);
  background-repeat: no-repeat;
  background-size: 27px 108px;
  background-position: center -68px;
  right: 6px;
}
.button2 {
  position: absolute;
  z-index: 0;
  top: 11px;
  width: 49px;
  height: 52px;
  background-image: url(/static/op.png);
  background-repeat: no-repeat;
  background-size: 27px 108px;
  background-position: center 12px;
  right: -45px;
}
.balance1 {
  position: relative;
  top: 0 !important;
  z-index: 1;
  background: white;
  width: 100%;
  height: 50px;
  line-height: 50px;
}
.carri1 {
  font-size: 15px;
  color: #999;
  margin-left: 80px;
}
.carjs1 {
  float: right;
  width: 115px;
  height: 100%;
  color: white;
  background: rgb(190, 190, 190);
  line-height: 50px;
  text-align: center;
}
.caremp {
  position: absolute;
  left: 0;
  top: 0px;
  width: 80px;
  height: 48px;
  background: url(/static/cart.png) no-repeat 10px 5px;
  background-size: 50px 125px;
  z-index: 2;
}
.checkboxall {
  position: absolute;
  height: 60px;
  width: 50px;
  top: -15px;
  left: 0px;
}
.checkboxall input[type="checkbox"] {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 20px;
  height: 20px;
  opacity: 0;
}
.checkboxall label {
  position: absolute;
  left: 30px;
  top: 0;
  height: 20px;
  line-height: 20px;
}
.checkboxall label:before {
  content: "";
  position: absolute;
  left: -20px;
  top: 20px;
  width: 20px;
  height: 20px;
  border: 1px solid #ddd;
  border-radius: 50%;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.checkboxall label:after {
  content: "";
  position: absolute;
  left: -13px;
  top: 23px;
  width: 6px;
  height: 12px;
  border: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  background: #fff;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.checkboxall input[type="checkbox"]:checked + label:before {
  background: #4cd764;
  border-color: #4cd764;
}
.checkboxall input[type="checkbox"]:checked + label:after {
  background: #4cd764;
}
.all span {
  margin-top: 5px;
  margin-bottom: 5px;
  display: inline-block;
  font-size: 14px;
  margin-left: 40px;
}
.checkbox {
  position: absolute;
  height: 60px;
  width: 50px;
  top: 6px;
  left: -10px;
}
.checkbox input[type="checkbox"] {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  opacity: 0;
}
.checkbox label {
  position: absolute;
  left: 30px;
  top: 0;
  height: 20px;
  line-height: 20px;
}
.checkbox label:before {
  content: "";
  position: absolute;
  left: -20px;
  top: 20px;
  width: 20px;
  height: 20px;
  border: 1px solid #ddd;
  border-radius: 50%;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}

.checkbox label:after {
  content: "";
  position: absolute;
  left: -13px;
  top: 23px;
  width: 6px;
  height: 12px;
  border: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  background: #fff;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}

.checkbox input[type="checkbox"]:checked + label:before {
  background: #4cd764;
  border-color: #4cd764;
}
.checkbox input[type="checkbox"]:checked + label:after {
  background: #4cd764;
}
.shopping {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding: 0 0 10px 30px;
  margin-left: 10px;
  line-height: 36px;
  color: #333;
  border-bottom: 1px solid #e8e8e8;
  background: #fff;
}
.all {
  border-bottom: 1px solid #ebebeb;
  position: fixed;
  width: 100%;
  z-index: 1;
  background: #fff;
}
.shopname {
  min-height: 36px;
  line-height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  float: left;
  margin-left: 67px;
  font-size: 15px;
}
.shoptbox {
  margin-top: 32px;
  background: #fff;
}
.tm {
  position: absolute;
  top: 0px;
  left: 44px;
  padding: 0 5px;
  height: 15px;
  line-height: 15px;
  overflow: hidden;
  text-align: center;
  color: #fff;
  background-color: #ff5d5c;
  border-radius: 7px;
  font-size: 10px;
}
.shopmuch {
  float: left;
  clear: both;
  min-height: 36px;
  line-height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 67px;
  font-size: 15px;
}
</style>



